<template>
  <div class="layout">
    <div class="layout-nav">
      <router-link class="btn-fixed-close" to="/member/"><i class="icon-close"></i></router-link>
      <div class="navbar_member">
        <h1 class="name">{{currentuser.name?currentuser.name:""}}</h1>
        <div class="job"><span>{{currentuser.job?currentuser.job:""}}</span></div>
        <div class="menu">
          <router-link :class="active === 1? 'active':''" :to="path_resume">个人简历</router-link>
          <router-link :class="active === 2? 'active':''" :to="path_product">绘画作品</router-link>
          <router-link :class="active === 3? 'active':''" :to="path_photo">图片摄影</router-link>
          <router-link :class="active === 4? 'active':''" :to="path_video">视频作品</router-link>
        </div>
      </div>
    </div>
    <div class="layout-content">
      <slot></slot>
    </div>
  </div>
</template>
<script>
  import {userdata} from '../mock-data/userdata.js'
  export default {
    components:{

    },
    data(){
      return {
        path_resume:"",
        path_product:"",
        path_photo:"",
        path_video:"",
        currentuser:{
          "name":"",
          "job":"",
        }
      }
    },
    props:['active','name','job','user'],
    created(){
      let name = this.$route.params.user;
      this.path_product = `/member/${name}/product`;
      this.path_resume = `/member/${name}`;
      this.path_photo = `/member/${name}/photo`;
      this.path_video = `/member/${name}/video`;

      var users = userdata;
      if(name&&users&&users[name]){
        this.currentuser = users[name];
      }
    }
  }
</script>

<style lang="less" scoped>
  .btn-fixed-close{
    position: fixed;
    float: right;
    right: 20px;
    top:20px;
    display:inline-block;
    width: 30px;
    height: 30px;
  }
  .navbar_member{
    position: absolute;
    bottom: 0;
    top:0;
    margin: auto;
    height: 224px;
    color:#fff;
    .name{font-size: 18px;padding-bottom: 20px;}
    .job{
      span{font-size: 12px;padding-bottom: 20px;border-bottom: 1px solid #fff;display: inline-block;}
    }
    .menu{
      line-height: 32px;
      padding-top: 30px;
      a{color:#545454;display: block;font-size: 12px;}
      a.active{color:#fff;}
    }
  }
</style>



